.blogCate-main .blogCate-inner{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow-x: hidden;
    /* padding-top: 145px; */
}

.blogCate-main .banner-content input {
    border-radius: 16px;
    padding: 7px 20px;
    border: unset;
    border: unset;
    width: 72%;
    background: transparent;
}
.blogCate-main .banner-content input :focus-visible{
  outline-offset: unset;
}
.blogCate-main .banner-content ::placeholder{
   color: #AAAAAA;
}
.blogCate-main .banner-content .input-container{
    background-color: #E6E6E6;
    height: 40px;
    width: 56%;
    border-radius: 20px;
}
.blogCate-main .banner-content .join {
    background-color: #01949A;
    padding: 8px 30px;
    border: unset;
    border-radius: 20px;
    position: relative;
    bottom: 38px;
    left: 239px;
    color: #ffff;
}

.blogCate-main .tabs-container{
    padding-top: 88px;
    padding-bottom: 0px;
}


.blogCate-main .tabs-container .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
        /* border-color: #dee2e6 #dee2e6 #fff; */
        background-color: #01949A;
        padding: 8px 24px;
        border: unset;
        border-radius: 16px;
        color: #ffff !important;
}

.blogCate-main .tabs-container .nav-tabs {
    border-bottom: unset;
}

.blogCate-main .tabs-container .nav-tabs .nav-item {
    margin-right: 12px;
}

.blogCate-main .tabs-container .nav-tabs .nav-link {
    margin-bottom: -1px;
    border: 1px solid transparent;
    border-top-left-radius: 0.25rem;
    border-top-right-radius: 0.25rem;
    color: #7A7A7A;
    border: 1px solid;
    border-radius: 16px;
}

